<template>
    <v-container fluid class="container" @click="timer_logout_set()">
        <layout-header/>
        <v-row class="contents" align="center" justify="center">
            <list-menu-list
                class="content-body"
                title="프리웨이트"
                :switchItems="switchItems"
                :thumbnailList="thumbnailList"
            />
        </v-row>
    </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex'
import exercise from '@/mixins/exercise.js'
import motionCamera from '@/mixins/motionCamera.js'
import timer_logout from '@/mixins/timer_logout.js'

export default {
    name: 'FreeWeight',
    mixins:[ exercise, timer_logout, motionCamera ],
    data:()=>({
        thumbnailList: []
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_EX_RESULT_LIST',
            'GET_MENU_PART_LIST',
        ]),
        switchItems(){
            return this.exercise_free_weight_switch_items
        }
    },
    async created() {
        await this.GetExResultList()
        await this.ACT_MENU_API_GET_PART_LIST()
        .then(()=>{
            this.EditSortList()
        })
    },
    methods: {
        ...mapActions([
            'ACT_MENU_API_GET_EX_RESULT_LIST',
            'ACT_MENU_API_GET_PART_LIST',
            'ACT_MENU_API_GET_PART_GUIDE',
        ]),
        ...mapMutations([
            'MU_MENU_PART_LIST',
        ]),
        async GetExResultList(){
            // 운동 이력 가져오기
            const exType = 0 // 서킷을 제외한 나머지는 0 (프리웨이트, 오늘의 운동, 스트레칭)
            await this.ACT_MENU_API_GET_EX_RESULT_LIST(exType)
        },
        async EditSortList(){
            for( const item of this.GET_MENU_PART_LIST){
                item.name       = item.exSortName
                item.link       = 'PartPlay'
                item.tabType    = item.bodyPartCode
                item.switchType = 0

                // 운동 이력 체크
                let check = this.GET_MENU_EX_RESULT_LIST.exList.find(list => list.exSortCode === item.exSortCode)
                if(check !== undefined) item.check = true

                // skeleton 체크
                let skeleton = this.exercise_skeleton_items.find(list=> list.exSortCode === item.exSortCode)
                if(skeleton !== undefined) item.skeleton = true
                
                // motion mode check
                item.motion = this.motionCamera_checkExercise(item.exSortCode)

                // 이미지 추가
                item.image  = this.exercise_create_image(item.exSortCode)
                
                // video 추가
                item.video = this.exercise_create_ex_video(item.exSortCode)
                
                if(item.video !== undefined) this.thumbnailList.push(item)
                
                // // Guide 추가 가이드가 존재하지 않는다고 출력
                // let Guide  = await this.ACT_MENU_API_GET_PART_GUIDE(item.exSortCode)
                // console.log('Guide',Guide)

            }
            
            this.MU_MENU_PART_LIST(this.thumbnailList)
            // console.log('this.thumbnailList',this.thumbnailList)
            // console.log('check',this.GET_MENU_EX_RESULT_LIST)
        },
    }
}
</script>

